import { string, object, setLocale } from "yup";
import { ptForm } from "yup-locale-pt";

setLocale(ptForm);

const ingressoSchema = object({
  id: string(),
  eventoId: string().required(),
  nome: string().required(" necessrio ter um nome."),
  horario: string().required(" necessrio ter um horrio.").matches(/^[0-9]{2}:[0-9]{2}$/, "O horrio deve ser no formato hh:mm"),
  data: string().required(" necessrio ter uma data.").matches(/^[0-9]{2}\/[0-9]{2}\/[0-9]{2}$/, "A data deve ser no formato dd/mm/aa"),
  descricao: string().required(" necessrio ter uma descrio."),
});

export default ingressoSchema;
