import { string, object, setLocale, number } from "yup";
import { ptForm } from "yup-locale-pt";

setLocale(ptForm);

const vendaSchema = object({
  id: string(),
  ingressoId: string().required(),
  valor: number().required(" necessrio ter um valor."),
  quantidade: number().required(" necessrio ter uma quantidade."),
});

export default vendaSchema;
