import { string, object, setLocale } from "yup";
import { ptForm } from "yup-locale-pt";

setLocale(ptForm);

const compraSchema = object({
  id: string(),
  cpf: string().required(" necessrio ter um CPF.").matches(/^[0-9]{3}.[0-9]{3}.[0-9]{3}-[0-9]{2}$/, "CPF invlido."),
  nome_do_cartao: string().required(" necessrio ter o nome do dono do carto."),
  numero_do_cartao: string().required(" necessrio ter o nmero do carto.").matches(/[0-9]{16}/, "Nmero do carto invlido."),
  data_de_validade: string().required(" necessrio ter a data de validade do carto.").matches(/[0-9]{2}\/[0-9]{2}/, "Data de validade invlida."),
  codigo_de_seguranca: string().required(" necessrio ter o cdigo de segurana do carto.").matches(/[0-9]{3}/, "Cdigo de segurana invlido."),
});

const editarCompraSchema = object({
  id: string(),
  cpf: string().required(" necessrio ter um CPF.").matches(/^[0-9]{3}.[0-9]{3}.[0-9]{3}-[0-9]{2}$/, "CPF invlido.")
})

export { compraSchema, editarCompraSchema };