import { string, object, setLocale } from "yup";
import { ptForm } from "yup-locale-pt";

setLocale(ptForm);

const eventoSchema = object({
  id: string(),
  nome: string().required(" necessrio ter um nome."),
  genero: string().required(),
  endereco: string().required(" necessrio ter um endereo."),
  local: string().required(" necessrio ter um local."),
});

export default eventoSchema;
