import { string, object, setLocale } from "yup";
import { ptForm } from "yup-locale-pt";

setLocale(ptForm);

const criarUsuarioSchema = object({
  id: string(),
  username: string().required(" necessrio ter um nome."),
  email: string().required(" necessrio ter um local."),
  password: string().required(" necessrio ter uma senha."),
});

const entrarUsuarioSchema = object({
  id: string(),
  email: string().required(" necessrio ter um local."),
  password: string().required(" necessrio ter uma senha."),
});

export { criarUsuarioSchema, entrarUsuarioSchema };
